* {
    box-sizing: border-box;
}

a {
    text-decoration: none;
    width: 100px;
    height: 100px;
}


a:hover,
a:focus,
a:link {
    text-decoration: none;
}

.card {
    width: 430px;
    height: 430px;
    box-sizing: border-box;
    background-color: var(--bg-card);
    margin: 0.5em;
    border-radius: 8px;
    border: 1px solid #e2e8f0;
    cursor: pointer;
}

.card_img {
    width: 430px;
    height: 272px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}


.card_content {
    padding: 12px;
    -webkit-box-shadow: 0px 22px 46px -26px rgba(45, 55, 72, 0.6);
    -moz-box-shadow: 0px 22px 46px -26px rgba(45, 55, 72, 0.6);
    box-shadow: 0px 22px 46px -26px rgba(45, 55, 72, 0.6);
    /* border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px; */
    background-color: var(--bg-card);
}

.card_content:hover {
    transform: translateZ(12px);
    -webkit-box-shadow: 0px 22px 46px -19px rgba(45, 55, 72, 0.6);
    -moz-box-shadow: 0px 22px 46px -19px rgba(45, 55, 72, 0.6);
    box-shadow: 0px 22px 46px -19px rgba(45, 55, 72, 0.6);
}

.card_title {
    padding: 7px;
    font-size: 1.4em;
    font-weight: 800;
    width: 100%;
    color: var(--text-secondary-1);
    background-color: var(--bg-card);
}

.card_description {
    padding: 7px;
    width: 100%;
    font-size: 1.2em;
    font-weight: 500;
    line-height: 30px;
    color: var(--text-secondary-2);
    background-color: var(--bg-card);
}

