* {
    box-sizing: border-box;
}

.project_section {
    width: 100%;
    min-height: 100vh;
    margin: 2.5em 0 3em 0;
    padding: 0;
    background-color: var(--bg-primary);
}

.projects_intro {
    width: 88%;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    color: var(--text-secondary-1);
}

.project_intro_heading {
    margin-right: 0.8em;
    height: 100%;
    font-weight: 500;
    font-size: 3.2em;
}

.project_intro_span {
    font-family: nanum-pen, sans-serif;
    width: 40%;
    font-weight: 400;
    font-style: normal;
    font-size: 2.4em;
    padding-top: 0.5em;
}

.card_section {
    max-width: 88%;
    min-height: 100vh;
    margin: 0.5em auto 0.5em auto;
    padding: 0.5em;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

@media(max-width: 1480px) {
    .project_section {
        width: 100%;
        min-height: 100vh;
        margin: 2.5em 0 3em 0;
        padding: 0;
        background-color: var(--bg-primary);
    }

    .projects_intro {
        width: 88%;
        margin: 0 auto;
        display: flex;
        justify-content: flex-start;
        color: var(--text-secondary-1);
    }

    .project_intro_heading {
        margin-right: 0.8em;
        height: 100%;
        font-weight: 500;
        font-size: 3em;
    }

    .project_intro_span {
        font-family: nanum-pen, sans-serif;
        width: 40%;
        font-weight: 400;
        font-style: normal;
        font-size: 2.2em;
        padding-top: 0.6em;
    }

    .card_section {
        max-width: 88%;
        min-height: 100vh;
        margin: 0.5em auto 0.5em auto;
        padding: 0.6em;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}

@media(max-width:1024px) and (min-width:768px) {
    .project_intro_heading {
        margin-right: 0.8em;
        height: 90%;
        font-weight: 500;
        font-size: 2.8em;
    }

    .project_intro_span {
        font-family: nanum-pen, sans-serif;
        width: 40%;
        font-weight: 400;
        font-style: normal;
        font-size: 2em;
        padding-top: 0.8em;
    }
}

@media(max-width:912px) {

    .projects_intro {
        width: 92%;
        margin: 0 auto;
        display: flex;
        justify-content: flex-start;
    }

    .card_section {
        max-width: 92%;
        min-height: 100vh;
        margin: 0 auto 0 auto;
        padding: 0.2em;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    .project_intro_heading {
        margin-right: 0.8em;
        margin-left: 0.4em;
        height: 100%;
        font-weight: 500;
        font-size: 3em;
    }
}

@media(max-width:863px) and (min-width:768px) {
    .projects_intro {
        width: 94%;
        margin: 0 auto;
        display: flex;
        justify-content: flex-start;
        margin-bottom: 1em;
    }

    .card_section {
        max-width: 94%;
        min-height: 90vh;
        margin: 0 auto 0 auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 0;
    }

    .project_intro_heading {
        margin-right: 0.8em;
        margin-left: 0.4em;
        height: 100%;
        font-weight: 500;
        font-size: 3em;
    }
}

@media(max-width:768px) {
    .projects_intro {
        width: 94%;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 1em;
    }

    .card_section {
        max-width: 94%;
        min-height: 100vh;
        margin: 0 auto 0 auto;
        padding: 0.2em;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    .project_intro_heading {
        margin-right: 0.4em;
        height: 100%;
        font-weight: 500;
        font-size: 2.4em;
    }

    .project_intro_span {
        font-family: nanum-pen, sans-serif;
        width: 55%;
        font-weight: 400;
        font-style: normal;
        font-size: 2.4em;
        padding-top: 0.5em;
    }
}

@media(max-width:545px) {
    .projects_intro {
        width: 96%;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 1em;
    }

    .project_intro_heading {
        margin-right: 0.4em;
        height: 100%;
        font-weight: 500;
        font-size: 2em;
    }

    .project_intro_span {
        font-family: nanum-pen, sans-serif;
        width: 55%;
        font-weight: 400;
        font-style: normal;
        font-size: 1.8em;
        padding-top: 0.5em;
    }
}

@media(max-width:414px) {
    .projects_intro {
        width: 98%;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 1em;
    }

    .project_intro_heading {
        margin-right: 0.4em;
        height: 100%;
        font-weight: 500;
        font-size: 1.8em;
    }

    .project_intro_span {
        font-family: nanum-pen, sans-serif;
        width: 56%;
        font-weight: 400;
        font-style: normal;
        font-size: 1.6em;
        padding-top: 0.5em;
    }
}